
































import { Component, Vue } from 'vue-property-decorator';

@Component({
	data: function() {
		return {};
	},
	props: {
		project: Object
	},
	methods: {
		isPersonal(type: string): string {
			return type === 'Personal' ? '#0277BD' : '#b21b57';
		}
	},
	computed: {
		isMobile() {
			return window.innerWidth < 600 ? true : false;
		}
	}
})
export default class TimelineCard extends Vue {}
